import React, { useState, useRef } from "react";
import styles from "./Discussions.module.css";
import classNames from "classnames";
import {
  Avatar,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  IconButton,
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  Tooltip,
  Switch,
  Grid,
  CircularProgress,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import DoneAllIcon from "@material-ui/icons/DoneAll";
import SendIcon from "@material-ui/icons/Send";
import QuestionAnswerIcon from "@material-ui/icons/QuestionAnswer";
import AddPhotoAlternateIcon from "@material-ui/icons/AddPhotoAlternate";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import VideocamOutlinedIcon from "@material-ui/icons/VideocamOutlined";
import Skeleton from "@material-ui/lab/Skeleton";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const Discussions = ({
  module_id,
  threads,
  createThread,
  followUp,
  fetchSlots,
  slots,
  course_slug,
  profile,
}) => {
  const [commentInput, setCommentInput] = useState("");
  const [newDoubtOpen, setNewDoubtOpen] = useState(false);
  const [isInstructor, setIsInstructor] = useState(true);
  const [isAnswer, setIsAnswer] = useState([]);
  const [snackOpen, setSnackOpen] = useState(false);
  const [open, setOpen] = useState(new Array(threads.length).fill(false));

  const handleCloseNewDoubt = () => {
    setNewDoubtOpen(false);
  };

  const slotBooked = () => {
    setSnackOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackOpen(false);
  };

  const handleNewSubmit = (newData) => {
    if (newData?.question) {
      // let tempData = {
      //   ...newData,
      //   question: newData.question,
      //   desc: newData.desc,
      //   date: newData.date,
      //   comments: [],
      //   answered: false,
      //   answer: null,
      // };
      createThread(
        module_id,
        newData.question,
        newData.desc,
        newData.images,
        null,
        newData.timeSlot.id,
        null,
        slotBooked
      );
    }
  };

  const handleImageClick = (src) => {
    const a = document.createElement("a");
    a.href = src;
    a.target = "_blank";
    a.download = true;
    a.click();
  };

  const answer = (id, index) => {
    let isFinal = false;
    if (isAnswer.includes(id)) isFinal = true;
    followUp(id, commentInput, null, isFinal);
    document.getElementById("comment" + id).value = "";
    let newOpen = [...open];
    newOpen[index] = true;
    console.log("seeetttttttttinggggggg oppeeen");
    setOpen(newOpen);
  };

  const commentForm = useRef();

  const handleEnter = (e) => {
    e.preventDefault();
    if (e.key === "Enter" || e.keyCode === 13) {
      commentForm.current.submit();
    }
  };

  const handleSubmit = (e, id, index) => {
    e.preventDefault();
    answer(id, index);
  };

  const handleToggleIsAnswer = (id) => {
    const index = isAnswer.findIndex((item) => item === id);
    if (index === -1) {
      setIsAnswer([...isAnswer, id]);
    } else {
      setIsAnswer(isAnswer.filter((item) => item !== id));
    }
  };

  return (
    <div className={styles.container}>
      <Snackbar
        open={snackOpen}
        anchorOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        autoHideDuration={6000}
        onClose={handleClose}
      >
        <Alert onClose={handleClose} severity="success">
          Your slot has been booked, For more detail check your registered
          email.
        </Alert>
      </Snackbar>
      <div
        className={classNames(
          styles.flexRow,
          styles.justifyBetween,
          styles.discussionHeading
        )}
      >
        <h2>Discussion Forum</h2>
        <Button
          onClick={() => setNewDoubtOpen(true)}
          variant="outlined"
          className={classNames(styles.doubtBtn, styles.doubtBtnLarge)}
        >
          New Doubt &nbsp;
          <QuestionAnswerIcon fontSize="small" />
        </Button>
        <Tooltip
          title="New Doubt"
          className={classNames(styles.doubtBtn, styles.doubtBtnSmall)}
          placement="top"
        >
          <IconButton onClick={() => setNewDoubtOpen(true)}>
            <QuestionAnswerIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </div>
      <div className={classNames(styles.discussions, styles.flexColumn)}>
        {threads.map((item, i) => {
          return (
            <div key={item.title + i} className={styles.questionContainer}>
              <div className={styles.question}>
                <div className={styles.profile}>
                  <Avatar>AP</Avatar>
                </div>
                <div className={styles.details}>
                  <h4>{item.title}</h4>
                  <p>{item.date}</p>
                  <p>{item.description}</p>
                  <p className={styles.questionDesc}>{item.desc}</p>
                  {item.image && (
                    <div className={styles.descImages}>
                      <Grid container spacing={2}>
                        {/* {item.images.map((imgItem) => ( */}
                        <Grid item md={3}>
                          <div className={styles.descImage}>
                            <img
                              onClick={() => handleImageClick(item.image)}
                              src={item.image}
                              alt="desc"
                            />
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  )}
                </div>
              </div>
              {item.followups && item.followups.length > 0 && (
                <Comments
                  data={item.followups}
                  isAnswered={
                    item.followups.filter((item) => item.is_final_answer)
                      .length > 0
                  }
                  answer={
                    item.followups.filter((item) => item.is_final_answer)[0]
                  }
                  open={open}
                  setOpen={setOpen}
                  index={i}
                />
              )}
              {profile.username === item.user || profile.is_staff ? (
                <form
                  onSubmit={(e) => handleSubmit(e, item.id, i)}
                  className={styles.inputContainer}
                  ref={commentForm}
                >
                  <input
                    // value={commentInput}
                    onChange={(e) => setCommentInput(e.target.value)}
                    className={styles.inputElement}
                    placeholder="Comment"
                    required
                    id={"comment" + item.id}
                    // onKeyUp={handleEnter}
                    type="text"
                  />
                  <div className={styles.actionBtns}>
                    <input
                      type="submit"
                      style={{
                        visibility: "hidden",
                        width: 0,
                        position: "absolute",
                        pointerEvents: "none",
                      }}
                    />
                    {profile.is_staff && (
                      <Switch
                        checked={isAnswer.includes(item.id)}
                        onChange={() => handleToggleIsAnswer(item.id)}
                        name="checkedA"
                        inputProps={{ "aria-label": "secondary checkbox" }}
                      />
                    )}
                    <IconButton type="submit">
                      <SendIcon />
                    </IconButton>
                  </div>
                </form>
              ) : null}
            </div>
          );
        })}
      </div>
      <NewDoubtModal
        open={newDoubtOpen}
        handleClose={handleCloseNewDoubt}
        handleNewSubmit={handleNewSubmit}
        fetchSlots={fetchSlots}
        slots={slots}
        course_slug={course_slug}
      />
    </div>
  );
};

export default Discussions;

const Comments = ({ data, isAnswered, answer, open, setOpen, index }) => {
  return (
    <Accordion
      className={styles.accordianContainer}
      onChange={(e, a) => {
        let newOpen = [...open];
        newOpen[index] = a;
        setOpen(newOpen);
      }}
      expanded={open[index]}
    >
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <div className={styles.accordianSummary}>
          <h3>
            {isAnswered ? "Answered" : "Not answered yet"}&nbsp;
            <span>
              {isAnswered ? <DoneAllIcon fontSize="small" /> : ""}
            </span>{" "}
          </h3>
          {isAnswered && (
            <p
              className={
                open[index] ? styles.answerExpanded : styles.answerCollapsed
              }
            >
              {answer.description}
            </p>
          )}
        </div>
      </AccordionSummary>
      <AccordionDetails>
        <div className={styles.descContainer}>
          {data.map((item, i) => {
            return (
              <div key={item.description + i} className={styles.question}>
                <div className={styles.profile}>
                  <Avatar>AP</Avatar>
                </div>
                <p>{item.description}</p>
              </div>
            );
          })}
        </div>
      </AccordionDetails>
    </Accordion>
  );
};

const NewDoubtModal = ({
  open,
  handleClose,
  handleNewSubmit,
  fetchSlots,
  slots,
  course_slug,
}) => {
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");
  const [images, setImages] = useState([]);

  //For slots
  const [timeSlots, setTimeSlots] = useState([]);
  const [showTimeSlots, setShowTimeSlots] = useState(false);
  const [loading, setLoading] = useState(false);
  const [selectedSlot, setSelectedSlot] = useState({});

  React.useEffect(() => {
    setTitle("");
    setDesc("");
    setImages([]);
    setTimeSlots([]);
    setShowTimeSlots(false);
    setLoading(false);
    setSelectedSlot({});
  }, [open]);

  const handleChangeImage = (e) => {
    const image = e.target.files[0];
    const reader = new FileReader();
    if (image) {
      try {
        reader.readAsDataURL(image);
      } catch (error) {}
      reader.onloadend = () => {
        if (reader.result)
          setImages([
            ...images,
            {
              title: image.name,
              src: reader.result,
              file: image,
            },
          ]);
      };
    }
  };

  const handleClickDelete = (src) => {
    setImages(images.filter((item) => item.src !== src));
  };

  //TODO
  //Form submit, use this function to send data to backend
  const handleSubmit = (e) => {
    e.preventDefault();
    // setShowTimeSlots(false);
    const formData = new FormData();
    let data = {
      question: title,
      desc,
      isLiveDoubt: Boolean(selectedSlot),
      date: new Date().toISOString(),
    };
    if (data.isLiveDoubt) data["timeSlot"] = selectedSlot;
    if (images && images.length) {
      images.forEach((item) => {
        formData.append("image", item.file); //change the key acc. to backend
      });
      // data["images"] = images.map((item) => item.src);
      data["images"] = images[0];
    }
    handleNewSubmit(data);
    handleClose();
  };

  const successSlots = (slots) => {
    console.log("succceessss", slots);
    setTimeSlots(slots);
    let tempSlots = slots.map((slot) => {
      let date = new Date(slot.starts_at);
      let dValue = `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}`;
      let time = `${date.getHours()}: ${date.getMinutes()}`;
      slot.date = dValue;
      slot.time = time;
      return slot;
    });
    setTimeSlots(tempSlots);
    setLoading(false);
  };

  const handleVideoClick = async () => {
    setLoading(true);
    await fetchSlots(course_slug, successSlots);
    // const date = new Date();
    // const dValue = `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}`;
    // setTimeSlots([
    //   {
    //     date: dValue,
    //     time: "03:00 PM",
    //   },
    //   {
    //     date: dValue,
    //     time: "03:00 PM",
    //   },
    //   {
    //     date: dValue,
    //     time: "03:00 PM",
    //   },
    // ]);
    setShowTimeSlots(true);
    // setTimeout(() => {
    //   setLoading(false);
    // }, 2000);
  };

  const handleClickSlot = (slot) => {
    if (selectedSlot === slot) {
      setSelectedSlot({});
    } else {
      setSelectedSlot(slot);
    }
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      style={{ zIndex: 999999 }}
      className={styles.dialogContainer}
    >
      <form onSubmit={handleSubmit}>
        <DialogTitle id="alert-dialog-title">{"Ask a doubt"}</DialogTitle>
        <DialogContent>
          <div className={styles.dialogContentContainer}>
            <div
              className={classNames(
                styles.modalInputContainer,
                styles.flexColumn
              )}
            >
              <label htmlFor="title">Question</label>
              <input
                name="title"
                required
                value={title}
                type="text"
                onChange={(e) => setTitle(e.target.value)}
              />
            </div>
            <div
              className={classNames(
                styles.modalInputContainer,
                styles.flexColumn
              )}
            >
              <label htmlFor="desc">Description</label>
              <textarea
                name="desc"
                required
                value={desc}
                type="text"
                minLength="6"
                onChange={(e) => setDesc(e.target.value)}
              />
            </div>
            <div className={styles.addImagesContainer}>
              {images.map((image) => {
                return (
                  <div key={image.title} className={styles.imgContainer}>
                    <img src={image.src} alt="question" />
                    <IconButton
                      onClick={() => handleClickDelete(image.src)}
                      className={styles.deleteBtn}
                    >
                      <HighlightOffIcon fontSize="small" />
                    </IconButton>
                  </div>
                );
              })}
              {/* <Button className={styles.addImagesBtn} variant="contained">
              <AddPhotoAlternateIcon />
            </Button> */}
              <Button
                className={styles.addImagesBtn}
                variant="contained"
                component="label"
                onClick={() => {
                  if (images.length === 1) {
                    alert("Can add single images");
                  }
                }}
              >
                <AddPhotoAlternateIcon />
                {images.length < 1 && (
                  <input
                    type="file"
                    id="file"
                    name="image"
                    onChange={handleChangeImage}
                    accept="image/jpg, image/png, image/jpeg"
                    style={{ width: "0", opacity: "0" }}
                  />
                )}
              </Button>
            </div>
            {showTimeSlots && (
              <div className={styles.timeSlotContainer}>
                <h4>Select Time Slot</h4>
                <Grid container className={styles.slots}>
                  {console.log(timeSlots)}
                  {loading ? (
                    <CircularProgress />
                  ) : (
                    timeSlots.map((item, i) => (
                      <Grid key={item.date + i} item md={3}>
                        <Button
                          onClick={() => handleClickSlot(item)}
                          variant="outlined"
                          className={classNames(
                            styles.slot,
                            selectedSlot === item ? styles.slotSelected : ""
                          )}
                        >
                          <h5>{item.date}</h5>
                          <p>{item.time}</p>
                        </Button>
                      </Grid>
                    ))
                  )}
                  {/* {timeSlots.map((item, i) =>
                    !loading ? (
                      <Grid key={item.date + i} item md={3}>
                        <Button
                          onClick={() => handleClickSlot(item)}
                          variant="outlined"
                          className={classNames(
                            styles.slot,
                            selectedSlot === item ? styles.slotSelected : ""
                          )}
                        >
                          <h5>{item.date}</h5>
                          <p>{item.time}</p>
                        </Button>
                      </Grid>
                    ) : (
                      <CircularProgress />
                    )
                  )} */}
                </Grid>
              </div>
            )}
          </div>
        </DialogContent>
        <DialogActions>
          <Tooltip
            className={styles.tooltip}
            placement="top"
            title="Live Doubt Discussion"
          >
            <Button onClick={handleVideoClick} className={styles.liveDisBtn}>
              Book Live Slot&nbsp;
              <VideocamOutlinedIcon fontSize="small" />
            </Button>
          </Tooltip>
          <Button
            className={classNames(styles.actionBtn, styles.negative)}
            onClick={handleClose}
          >
            Cancel
          </Button>
          <Button
            className={classNames(styles.actionBtn, styles.positive)}
            type="submit"
            autoFocus
          >
            Submit
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};
