import React, { useState, useEffect } from "react";
import { Button } from "@material-ui/core";
import styles from "./Updates.module.css";
import moment from "moment";
import Countdown from "react-countdown";
import classNames from "classnames";
import axios from "axios";

const Updates = () => {
  // const data = [
  //   {
  //     time: Date.now() + Math.floor(Math.random() * 1000000000),
  //     isDone: true,
  //   },
  //   {
  //     time: Date.now() + Math.floor(Math.random() * 1000000000),
  //     isDone: false,
  //   },
  //   {
  //     time: Date.now() + Math.floor(Math.random() * 1000000000),
  //     isDone: false,
  //   },
  //   {
  //     time: Date.now() + Math.floor(Math.random() * 1000000000),
  //     isDone: true,
  //   },
  // ];

  const [data, setData] = useState([]);

  const [timers, setTimers] = useState([
    // {
    //   isDone: false,
    // },
    // {
    //   isDone: false,
    // },
    // {
    //   isDone: false,
    // },
    // {
    //   isDone: false,
    // },
  ]);
  //TODO: SHWETA -> CALL FROM REDUX
  useEffect(() => {
    axios
      .get("/forum/discussion-slots", {
        params: { course_slug: "test-course-1" },
      })
      .then((res) => {
        if (res.data) {
          setData(res.data);
          setTimers(
            res.data.map((item) => {
              return {
                isDone: new Date(item.starts_at) > new Date().getDate(),
              };
            })
          );
        }
      });
    // fetch("/forum/discussion-slots?course_slug=test-course-1", {
    //   method: "GET",
    // }).then((res) => console.log(res));
  }, []);

  const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      // Render a completed state
      return <span>You can now join the meeting</span>;
    } else {
      // Render a countdown
      return (
        <span>
          {hours} Hr {minutes} Min {seconds} Sec
        </span>
      );
    }
  };

  const handleCompleteTimer = (e, item) => {
    if (e.completed) {
      const index = data.findIndex(
        (innerItem) => innerItem.time === item.starts_at
      );
      const newItem = timers[index];
      const newData = [...timers];
      newData.splice(index, 1, { ...newItem, isDone: e.completed });
      console.log({ newData });
      setTimeout(() => {
        setTimers(newData);
      }, 0);
    }
  };

  return (
    <div className={styles.container}>
      <h2>Updates</h2>
      <div className={styles.quizesListContainer}>
        <div className={styles.quizes}>
          {/* <h3>Documents</h3> */}
          <ul className={styles.quizesList}>
            {data.map((item, i) => {
              const mDate = new Date(item.starts_at);
              return (
                <li
                  key={item.starts_at.toString() + i.toString()}
                  className={styles.quiz}
                >
                  <div className={styles.details}>
                    <div>
                      <h4>{`${
                        moment(mDate)
                          .utcOffset("+05:30")
                          .toString()
                          .split(" GMT+0530")[0]
                      }`}</h4>
                      {mDate.getDate() - new Date().getDate() <= 1 && (
                        <p>
                          Starts in{" "}
                          <span>
                            <Countdown
                              date={mDate}
                              daysInHours
                              renderer={renderer}
                              onComplete={(e) => handleCompleteTimer(e, item)}
                            />
                          </span>
                        </p>
                      )}
                    </div>
                    <div>
                      <Button
                        disabled={!timers[i]?.isDone}
                        className={classNames(
                          styles.joinNowBtn,
                          !timers[i]?.isDone ? styles.disabled : ""
                        )}
                      >
                        Join
                      </Button>
                    </div>
                  </div>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Updates;
